@import '../mixins';

.slider-section {
    padding: 120px 0px;

    &__heading {
        color: var(--White, #FFF);

        /* Desktop/H3 */
        font-family: Inter Tight;
        font-size: 20px;
        font-style: normal;
        font-weight: 700;
        line-height: normal;
        letter-spacing: 0.4px;
        margin-bottom: 32px
    }
    .item{
        text-decoration: none;
    }
    &__bg {
        min-height: 428px;
        background-size: cover;
        display: flex;
        clip-path: polygon(5% 0, 100% 0%, 95% 100%, 0% 100%);
    }

    &__body {
        margin-top: auto;
        padding: 0px 45px 111px;


        &__heading {
            color: #FFF;
            font-family: Golos Text;
            font-size: 28px;
            font-style: normal;
            font-weight: 700;
            line-height: normal;
            text-transform: uppercase;
            margin-bottom: 0;
        }

        &__paragarph {
            color: #FFF;
            font-family: Inter Tight;
            font-size: 24px;
            font-style: normal;
            font-weight: 400;
            line-height: 30px;
            letter-spacing: 0.48px;

            p {
                margin-bottom: 0;
            }
        }
    }

    .owl-carousel .owl-nav button.owl-prev,
    .owl-carousel .owl-nav button.owl-next {
        z-index: 1;
        position: absolute;
        background-color: transparent;
        top: 50%;
        transform: translatey(-50%);
    }

    .owl-theme .owl-nav [class*='owl-'] {
        padding: 32px 8px;
        background: rgba(0, 0, 0, 0.40);
        border-radius: 0;
        transition: all .3s ease;

        &:hover {
            background: rgba(0, 0, 0, 1);
        }


    }

    .owl-carousel .owl-nav button.owl-prev {
        left: 0;
    }

    .owl-carousel .owl-nav button.owl-next {
        right: 0;
    }

    .owl-dots {
        margin: 32px  0;

        .owl-dot {
            span {
                width: 71px !important;
                height: 6px !important;
                border-radius: 0 !important;
                background: #3F3F3E !important;
                opacity: 1;
            }
        }

        .owl-dot.active {
            span {
                background: #EEEEEE !important;
            }
        }
    }
}